<template>
  <b-row>
    <b-col cols="12">
      <h2>Board List
        <b-link href="#/add-board">(Add Board)</b-link>
      </h2>
      <b-table striped hover :items="boards" :fields="fields">
        <template slot="actions" scope="row">
          <b-btn size="sm" @click.stop="details(row.item)">Details</b-btn>
        </template>
      </b-table>
    </b-col>
  </b-row>
</template>

<script>
import firebase from "../firebase";
import router from "../router";

export default {
  name: "BoardList",
  data() {
    return {
      fields: {
        name: { label: "Name", sortable: true, class: "text-left" },
        guest: { label: "Guest", sortable: true, class: "text-left" },
        rsvp: { label: "RSVP", sortable: true, class: "text-left" },
        actions: { label: "Action", class: "text-center" }
      },
      boards: [],
      errors: [],
      ref: firebase.firestore().collection("boards")
    };
  },
  created() {
    this.ref.onSnapshot(querySnapshot => {
      this.boards = [];
      querySnapshot.forEach(doc => {
        this.boards.push({
          key: doc.id,
          name: doc.data().name,
          guest: doc.data().guest,
          rsvp: doc.data().rsvp
        });
      });
    });
  },
  methods: {
    details(board) {
      router.push({ name: "ShowBoard", params: { id: board.key } });
      router.push({ guest: "ShowBoard", params: { id: board.key } });
      router.push({ rsvp: "ShowBoard", params: { id: board.key } });
    }
  }
};
</script>

<style>
.table {
  width: 96%;
  margin: 0 auto;
}
</style>